<template>
  <v-container grid-list-sm fluid>
    <v-layout wrap>
    <v-flex sm8 offset-sm2>
      <v-select v-model="kneePain" :items="kneePains" label="" ></v-select>
    </v-flex>
    <v-flex sm2></v-flex>

    <v-flex sm8 offset-sm2>
      <v-select v-model="jointragin" :items="jointragins" label=" ROM " ></v-select>
    </v-flex>
    <v-flex sm2></v-flex>

    <v-flex sm8 offset-sm2>
      <v-select v-model="Anteroposterior" :items="Anteroposteriors" label="Antero-posterior " ></v-select>
    </v-flex>
    <v-flex sm2></v-flex>

    <v-flex sm8 offset-sm2>
      <v-select v-model="Mediolateral" :items="Mediolaterals" label=" Mediolateral " ></v-select>
    </v-flex>
    <v-flex sm2></v-flex>

    <v-flex sm8 offset-sm2>
      <v-select v-model="varusvalgus" :items="varusvalguses" label="  () varus & valgus" ></v-select>
    </v-flex>
    <v-flex sm2></v-flex>

    <v-flex sm8 offset-sm2>
      <v-select v-model="Flexioncontracture" :items="Flexioncontractures" label=" Flexion contracture ()" ></v-select>
    </v-flex>
    <v-flex sm2></v-flex>

    <v-flex sm8 offset-sm2>
      <v-select v-model="extensionlag" :items="extensionlags" label=" (extension lag) ()" ></v-select>
    </v-flex>
    <v-flex sm2></v-flex>

    <v-flex sm10 offset-sm1>
      <v-subheader>? (0~10) </v-subheader>
        <v-card-text>
          <v-slider
          thumb-label="always"
          step="1"
          v-model="pains"
          :max="10"
          ></v-slider>
      </v-card-text>
    </v-flex>
    <v-flex sm1></v-flex>

    <v-btn @click="text" dark></v-btn>

    <v-flex sm8 offset-sm2>
     {{toal}}
    </v-flex>
    <v-flex sm2></v-flex>
    </v-layout>
  </v-container>
</template>

<script>



export default {
 
  data: () => ({
    kneePains:[
      { text: "", value: "50" },
      { text: " ()", value: "45" },
      { text: " ( )", value: "40" },
      { text: "  ()", value: "30" },
      { text: " ()", value: "20" },
      { text: " ()", value: "10" },
      { text: "", value: "0" },
    ],
    kneePain:0,
    jointragins:[
      { text: "0~5", value: "1" },{ text: "6~10", value: "2" },{ text: "11~15", value: "3" },
      { text: "16~20", value: "4" },{ text: "21~25", value: "5" },{ text: "26~30", value: "6" },
      { text: "31~35", value: "7" },{ text: "36~40", value: "8" },{ text: "41~45", value: "9" },
      { text: "46~50", value: "10" },{ text: "51~55", value: "11" },{ text: "56~60", value: "12" },
      { text: "61~65", value: "13" },{ text: "66~70", value: "14" },{ text: "71~75", value: "15" },
      { text: "76~80", value: "16" },{ text: "81~85", value: "17" },{ text: "86~90", value: "18" },
      { text: "91~95", value: "19" },{ text: "96~100", value: "20" },{ text: "101~105", value: "21" },
      { text: "106~110", value: "22" },{ text: "111~115", value: "23" },{ text: "116~120", value: "24" },
      { text: "121~125", value: "25" },
    ],
    jointragin:0,
    Anteroposteriors:[
      { text: "< 5mm", value: "10" },
      { text: "5-10mm ", value: "5" },
      { text: ">10 mm ", value: "0" },
    ],
    Anteroposterior:0,
    Mediolaterals:[
      { text: "< 5", value: "15" },
      { text: "6~9 ", value: "10" },
      { text: "10~14 ", value: "5" },
      { text: ">15 ", value: "0" },
    ],
    Mediolateral:0,
    varusvalgus:0,
    varusvalguses:[
       { text: "0", value: "-15" },{ text: "1", value: "-12" },{ text: "2", value: "-9" },
      { text: "3", value: "-6" },{ text: "4", value: "-3" },{ text: "5~10", value: "0" },
      { text: "11", value: "-3" },{ text: "12", value: "-6" },{ text: "13", value: "-9" },
      { text: "14", value: "-12" },{ text: "15", value: "-15" },{ text: "15", value: "-20" },
    ],
    Flexioncontractures:[
      { text: "5~10", value: "-2" },
      { text: "10~15 ", value: "-5" },
      { text: "16~20 ", value: "-10" },
      { text: ">20 ", value: "-15" },
      { text: "", value: "0" },
    ],
    Flexioncontracture:0,
    extensionlag:0,
    extensionlags:[
      { text: "<10", value: "-5" },
      { text: "10~20 ", value: "-10" },
      { text: ">20 ", value: "-15" },
      { text: "", value: "0" },
    ],
    pains:0,
    toal:0  ,
  }),
  methods:
  {
    text: function () { 
      this.toal = parseInt(this.jointragin) + parseInt(this.kneePain) + parseInt(this.Anteroposterior) + parseInt(this.Mediolateral)
      + parseInt(this.varusvalgus) + parseInt(this.Flexioncontracture) + parseInt(this.extensionlag)
      console.log(this.toal);
      
    }
  }

  
};
</script>
